<template>
  <div id="advert" v-if="!user">
    <v-container class="mt-5">
      <v-layout row>
        <v-flex xs12 sm12 md12 class="pa-2">
          <v-card router to="/admin" flat class="text-center">
            <v-avatar color="#757575">
              <v-icon size="30" color="white">mdi-plus-circle-outline</v-icon>
            </v-avatar>
            <v-card-subtitle class="overline font-weight-bold"
              >Empty Click Here</v-card-subtitle
            >
          </v-card>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>
<script>
import { au } from "@/firebase/init";
export default {
  name: "Empty",
  data() {
    return {
      user: "",
    };
  },
    created() {
    au.onAuthStateChanged((user) => {
      if (user) {
        this.user = user;
      } else {
        this.user = null;
      }
    });
  },
}
</script>